import React from 'react'
import './Header.css'
import profilePicture from './assets/profilepicture.jpg';
import Cursive from './assets/Cursive.svg';


export default function Header() {
    return (
        <div className={'header'}>
            <object type="image/svg+xml" data={Cursive} id = {'nameHeader'} className={'name'}>svg-animation</object>
            <img src = {profilePicture} className={'profile-pic'}/>
            <h1><b>Frontend Software Engineer Intern</b></h1>
        </div>
    )
}
