import React from 'react'

export default function Contact() {
    return (
        <div className={'main-div'}>
            <h1>Contact Me</h1>
            <hr style={{ width: '50%' }} />

            <div className={"container"}>
                <form action={"https://formspree.io/f/mnqlnkqv"} method={"POST"} target={"_blank"}>

                    <div className={"form-group"}>
                        <div className={"input-group"}>
                            <div className={"input-group-prepend"}>
                                <span className={"input-group-text bg-white"}>
                                    <i className={"fa fa-user"}></i>
                                </span>
                            </div>
                            <input name={"name"} type={"name"} placeholder={"Name"} className={"form-control border-left-0"} required />
                        </div>
                    </div>

                    <div className={"form-group"}>
                        <div className={"input-group"}>
                            <div className={"input-group-prepend"}>
                                <span className={"input-group-text bg-white"}>
                                    <i className={"fa fa-envelope"}></i>
                                </span>
                            </div>
                            <input name={"email"} type={"email"} placeholder={"Email"} class={"form-control border-left-0"} required />
                        </div>
                    </div>

                    <div className={"form-group"}>
                        <textarea className={"form-control"} id={"message"} name={'message'} rows={"5"} placeholder={"Enter message"} required></textarea>
                    </div>

                    <button type={"submit"} className={"btn btn-primary float-right"}>
                        <i class="fa fa-paper-plane"></i>
                        Send
                    </button>
                </form>
            </div>
        </div>
    )
}
