
import './App.css';
import Description from './Description';
import Header from './Header'
import NavBar from './NavBar';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Resume from './Resume';
import Contact from './Contact'


function App() {
  return (
    <head><link rel={"stylesheet"} href={"https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css"} integrity={"sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm"} crossorigin={"anonymous"}/><link rel={'stylesheet'} href={"https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"} /></head>,
    <div className={'mainDiv wrapper'}>
    <NavBar/>
    <BrowserRouter>
        <Switch>
          <Route exact path="/">
          <Header/>
          <Description/>
          </Route>
          <Route path="/:resume">
          <Resume/>
          </Route>
          <Route path = "/:contact">
            <Contact/>
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
