import React from 'react';
import { Nav,Navbar,Container, NavDropdown } from 'react-bootstrap';
import nameIcon from './assets/icon.ico';
import { Redirect } from 'react-router';

export default function NavBar() {
    return (
        <Navbar style={{backgroundColor:'hsl(179, 29%, 64%)'}}>
  <Container>
    <Navbar.Brand href="/"><img src={nameIcon}/></Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto">
        <Nav.Link href="/" className={window.location.pathname == '/' && 'active'}>Home</Nav.Link>
        <Nav.Link href="https://github.com/chantlog" target={'_blank'} rel={"noopener noreferrer"}>Github</Nav.Link>
        <Nav.Link href="/resume" className={window.location.pathname == '/resume' && 'active'}>Resume</Nav.Link>
        <Nav.Link href="/contact" className={window.location.pathname == '/' && '/contact'}>Contact Me</Nav.Link>
      </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
       
    )
}
